.header {
  // height: 100vh;
  background-color: $main-dark;
  border-radius: 0 0 30px 30px;

  // &__img {
  //   height: 390px;
  //   background-image: url(../images/headerMin.jpg);
  //   width: 100%;
  //   background-size: cover;
  //   background-position: center;
