  &__content {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 147px;
    }
  }

  &__img {

    border-radius: 30px;
    width:100%;
    object-fit: cover;
    background-position: center;

    @include onDesktop {
      border-radius: 30px 0;
    }
  }

  &__button {
    height: 50px;
  }

  &__title {
    @extend %title;
    @extend %main-title;
    margin-bottom: 32px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: #FFFFFF;
    margin-bottom: 72px;
    display: block;
    max-width: 362px;

    @include onDesktop {
      margin-bottom: 83px;
    }
  }

  &__main {
    display: block;

    @include onDesktop {
      display: flex;
    }
  }
}
